<template>
    <div>
        <!-- 搜索导航 -->
        <div class="searchnav">
            <van-icon name="arrow-left" @click="back" />
            <p class="search" @click="searchShop">
                <van-icon name="search" /><span>全场50元起步</span>
            </p>
        </div>
        <!--  -->
        <div class="classify">
            <van-tree-select :items="items" :main-active-index.sync="active">
                <template #content>
                    <div v-for="(item, index) in shopClass" :key="item.categoryId">
                        <div v-if="active === index" class="rightnav">
                            <template>
                                <div v-for="v in item.secondLevelCategoryVOS " :key="v.categoryId">
                                    <p>{{ v.categoryName }}</p>
                                    <van-grid :column-num="3" :border="false">
                                        <van-grid-item v-for="value in v.thirdLevelCategoryVOS" :key="value.categoryId"
                                            icon="http://s.weituibao.com/1583591077131/%E5%88%86%E7%B1%BB.png"
                                            :text="value.categoryName" @click='commodity(value.categoryId)' />
                                    </van-grid>
                                </div>
                            </template>
                        </div>
                    </div>
                </template>
            </van-tree-select>
        </div>
    </div>
</template>

<script>
import { shopClass } from '@/api/index'
export default {
    name: "categary",
    props: [],
    mounted() {
        shopClass().then(data => {
            if (data.resultCode === 200) {
                this.shopClass = data.data;
                data.data.map(item => {
                    item.text = item.categoryName
                    this.items.push(item)
                    // console.log(this.items);
                })
                // console.log(this.shopClass);
            }
        })
    },
    data() {
        return {
            items: [],
            active: 0,
            shopClass: [],
            index: '',
        }
    },
    methods: {
        // 点击头部左上角<
        back() {
            this.$router.back()
        },
        // 点击搜索
        searchShop() {
            this.$router.push('/search')
        },
        //商品
        commodity(id) {
            //    console.log(id);
            this.$router.push('/search?id=' + id);
        }

    }
}
</script>

<style lang="less" scoped>
.searchnav {
    display: flex;
    height: 56px;
    line-height: 100%;
    background-color: white;
    border-bottom: .02667rem solid rgb(222, 221, 221);
    position: fixed;
    width: 100%;
    z-index: 300;

    /deep/.van-icon-arrow-left {
        line-height: 54px;
        padding: 0 15px;
        font-size: 20px;

    }

    .search {
        width: 80%;
        height: 0.53333rem;
        margin: 0.26667rem 0;
        padding: 0.13333rem 0;
        color: #232326;
        background: #f7f7f7;
        border-radius: 0.53333rem;
        display: flex;
        align-items: center;

        .van-icon-search {
            padding: 0 0.26667rem 0 0.53333rem;
            font-size: .45333rem
        }

        span {
            font-size: .32rem;
            color: #666;
        }
    }
}
.rightnav{
    padding-bottom: 50px;
}
.classify {
    padding-top: 60px;
    height: 100%;

    /deep/.van-tree-select__nav {
        height: 600px;

        .van-sidebar-item {
            padding: 20px 0px 20px 10px;
        }
    }

    /deep/.van-tree-select__content {
        height: 600px;
        width: 56px;

        p {
            padding: 20px 0px 20px;
            font-size: 18px;
        }

        .van-grid-item__content {
            margin-left: 5px;
            padding: 0px 0px;
            height: 84px;

            .van-grid-item__text {
                padding: 10px 0px;
                font-size: 15px;
                text-align: center;
            }
        }

        .three {
            margin-bottom: 60px;
        }
    }
}
</style>